import Axios from "axios";
import React, { Component } from "react";
import BootstrapTable from "react-bootstrap-table-next";
import paginationFactory from "react-bootstrap-table2-paginator";
import ToolkitProvider from "react-bootstrap-table2-toolkit";
import Skeleton from "react-loading-skeleton";
import { connect } from "react-redux";
import SelectSearch from "react-select-search";
import { Field, reduxForm } from "redux-form";
import Swal from "sweetalert2";

const maptostate = (state) => {
  return {
    BatalPengambilanTimbang: state.provinsi.BatalPengambilanTimbang,
  };
};
const renderField = ({
  input,
  label,
  type,
  limit,
  readOnly,
  meta: { touched, error, warning },
}) => (
  <div className="form-group">
    <label htmlFor="" className="text-black">
      {label}
    </label>
    <input
      {...input}
      type={type}
      className="form-control"
      maxLength={limit}
      readOnly={readOnly}
    />
    {touched &&
      ((error && <p className="invalid-feedback">{error}</p>) ||
        (warning && <p>{warning}</p>))}
  </div>
);
const server = process.env.REACT_APP_BACKEND_URL;
function hapus(dispatch, kode) {
  Swal.fire({
    title: "Anda Yakin ?",
    text: "Anda Akan Menghapus Data Ini ?",
    icon: "warning",
    showCancelButton: true,
    confirmButtonColor: "#3085d6",
    cancelButtonColor: "#d33",
    confirmButtonText: "Yes, delete it!",
  }).then((result) => {
    if (result.isConfirmed) {
      Axios.delete(server + "customer", {
        data: {
          kodeCustomer: kode,
        },
      })
        .then((res) =>
          Swal.fire({
            position: "top-end",
            icon: "success",
            text: "Data Berhasil Dihapus!",
            showConfirmButton: false,
            timer: 1500,
          })
        )
        .catch((err) =>
          Swal.fire({
            position: "top-end",
            icon: "error",
            text: "Data Gagal Dihapus!",
            showConfirmButton: false,
            timer: 1500,
          })
        );
    }
  });
}
class BatalPengambilanTimbang extends Component {
  constructor(props) {
    super(props);
    this.state = {
      items: [
        {
          value: "TERIMA SUPPLIER",
          name: "TERIMA SUPPLIER",
        },
        {
          value: "PENJUALAN",
          name: "PENJUALAN",
        },
        {
          value: "PINDAH BARANG",
          name: "PINDAH BARANG",
        },
        {
          value: "RETURN CUSTOMER",
          name: "RETURN CUSTOMER",
        },
      ],
      columns: [
        {
          dataField: "tanggal",
          text: "Tanggal",
        },
        {
          dataField: "namaBaki",
          text: "Nama Baki",
        },
        {
          dataField: "supplier",
          text: "Supplier",
        },
        {
          dataField: "cabang",
          text: "Cabang",
        },
        {
          dataField: "kodeBarang",
          text: "Kode Barang",
        },
        {
          dataField: "qty",
          text: "Qty",
        },
        {
          dataField: "bruto",
          text: "Berat(Gr)",
        },
        {
          dataField: "netto",
          text: "Netto(Gr)",
        },
        {
          dataField: "rp",
          text: "Ongkos(Rp)",
        },
        {
          dataField: "rp",
          text: "Ongkos(Rp)",
        },
        {
          dataField: "Aksi",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row) => {
            return (
              <div className="text-center">
                <button
                  className="btn btn-danger mr-2"
                  onClick={() => hapus(this.props.dispatch, row.namaBaki)}
                >
                  <i className="fa fa-trash mr-1"></i>
                  HAPUS
                </button>
              </div>
            );
          },
        },
      ],
      data: [
        {
          tanggal: "2020",
        },
      ],
    };
  }

  handleSubmit(data) {
    console.log(data);
  }
  selected(data) {
    console.log(data);
  }
  render() {
    const { handleSubmit } = this.props;
    return (
      <div className="container-fluid">
        <div className="row">
          <div className="col-lg-12">
            <div className="card">
              <div className="card-body">
                <form onSubmit={handleSubmit}>
                  <div className="row">
                    <div className="col-md-4 col-lg-4">
                      <Field
                        name="noCloseJual"
                        label="No Close Jual"
                        type="text"
                        component={renderField}
                      />
                    </div>
                    <div className="col-md-4 col-lg-4">
                      <Field
                        name="jenisTrx"
                        label="Jenis Transaksi"
                        type="text"
                        component={renderField}
                      />
                    </div>
                    <div className="col-md-4 col-lg-4">
                      <label htmlFor="">Nama Toko</label>
                      <SelectSearch
                        options={this.state.items}
                        search
                        placeholder="Silahkan Pilih"
                        onChange={(data) => this.selected(data)}
                      />
                    </div>
                    <div className="col-lg-12 col-md-12">
                      {this.state.data ? (
                        <ToolkitProvider
                          keyField="id"
                          data={this.state.data}
                          columns={this.state.columns}
                          search
                        >
                          {(props) => (
                            <div className="form-group mt-2">
                              <BootstrapTable
                                {...props.baseProps}
                                striped
                                hover
                                condensed
                                bordered={true}
                                headerClasses="header-table"
                                pagination={paginationFactory()}
                              />
                            </div>
                          )}
                        </ToolkitProvider>
                      ) : (
                        <Skeleton width={"100%"} height={100} />
                      )}
                    </div>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

BatalPengambilanTimbang = reduxForm({
  form: "BatalPengambilanTimbang",
  enableReinitialize: true,
})(BatalPengambilanTimbang);
export default connect(maptostate, null)(BatalPengambilanTimbang);
